import Utils from "../../js/utils.js";
import MessageSend from "../message-send/index.vue";

export default {
	name: "messages-view",
	props: ["textChannel", "guild"],
	components: {
		"message-send": MessageSend
	},
	created() {
		this.fetchData();
	},
	mounted() {
		this.$nextTick(() => {
			let messagesDom = $(".messages");
			let guildViewDom = $(".guild-view");
			let topbarDom = $(".topbar");
			let messageSendDom = $(".message-send");

			setTimeout(() => {
				console.log(messagesDom.height());
				messagesDom.scrollTop(messagesDom.prop("scrollHeight"));
			}, 2000);

			setInterval(() => {
				messagesDom.css(
					"height",
					guildViewDom.height() - topbarDom.height() - messageSendDom.height() + "px"
				);
			}, 500);
		});
	},
	watch: {
		"$route": "fetchData"
	},
	methods: {
		fetchData() {
			this.textChannel.messageHistory.get(50, (err, messages) => {
				this.messages = messages;
			});
		},
		hasSameAuthorPrev(i, msg) {
			return i != 0 && msg.author === this.messages[i-1].author;
		}
	},
	data() {
		return {
			messages: []
		};
	}
};