
import ChannelList from "../channel-list/index.vue";

import Utils from "../../js/utils";

export default {
	name: "guild-view",
	components: {
		"channel-list": ChannelList
	},
	created() {
		this.fetchData();
	},
	watch: {
		"$route": "fetchData"
	},
	methods: {
		fetchData() {
			this.dataService.getGuild(this.$route.params.guildId, (err, guild) => {
				this.guild = guild;
				if (this.$route.params.textChannelId) {
					guild.getTextChannel(this.$route.params.textChannelId, (err, textChannel) => {
						this.textChannel = textChannel;
					});
				}
			});
		}
	},
	data () {
		return {
			dataService: this.$parent.dataService,
			guild: null,
			textChannel: null
		};
	}
};