import Channel from "./Channel";


export default class Guild {

	constructor(obj) {
		this.id = 0;
		this.name = "";
		this.img = "";
		this.channels = [];
		this.dataService = null;
		if (obj) Object.assign(this, obj);
	}

	getChannels() {
		return this.channels;
	}

	getTextChannel(id, callback) {
		try {
			let channels = this.getChannels();
				
			let ret = null;
			for (let i in channels) {
				let el = channels[i];
				if (el.id == id) {
					ret = el;
					break;
				}
			}
			
			callback(null, ret);
		} catch(err) {
			callback(err, null);
		}
	}
}