import MockData from "./MockData";
import Friends from "./Friends";
import Message from "./Message";

const USE_MOCK_DATA = true;

export default class DataService {

	constructor(anchorApi) {
		this.anchorApi = anchorApi;
		if (USE_MOCK_DATA) {
			this.mockData = MockData(this);
		}
	}

	getGuilds(callback) {
		if (USE_MOCK_DATA) {
			callback(null, this.mockData.guilds);
		}
	}

	getGuild(id, callback) {
		try {
			this.getGuilds((err, guilds) => {
				if (err) throw err;
				
				let ret = null;
				for (let i in guilds) {
					let el = guilds[i];
					if (el.id == id) {
						ret = el;
						break;
					}
				}
				callback(null, ret);
			});
		} catch(err) {
			callback(err, null);
		}
	}

	getMessages(guildId, textChannelId, number, callback) {
		let messages;
		if (USE_MOCK_DATA) {
			messages = this.mockData.messages[guildId][textChannelId];
		}
		if (typeof messages !== "undefined") {
			let startI = messages.length-number;
			if (startI > 0) messages = messages.slice(startI);

			callback(null, messages);
		} else {
			callback(null, []);
		}
	}

	sendMsg(guildId, channelId, msg) {
		if (USE_MOCK_DATA) {
			this.mockData.messages[guildId][channelId].push(new Message({
				author: this.anchorApi.username,
				text: msg
			}));
		}
	}

	getFriends(callback) {
		let f = new Friends();
		if (USE_MOCK_DATA) {
			Object.assign(f, this.mockData.friends);
			callback(null, f);
		}
	}

}