
import ChannelList from "../channel-list/index.vue";

import Utils from "../../js/utils";

export default {
	name: "message-send",
	mounted() {
		$("#message-text").on("keydown", e => {
			if (e.key === "Enter") {
				this.send();
			}
		});
	},
	methods: {
		send() {
			let messagesDom = $(".messages");
			let textDom = $("#message-text");
			let text = textDom.val();
			textDom.val("");

			setTimeout(() => {
				messagesDom.scrollTop(messagesDom.prop("scrollHeight"));
			}, 500);


			this.textChannel.sendMsg(text);
		}
	},
	data () {
		return {
			dataService: this.$parent.dataService,
			guild: this.$parent.guild,
			textChannel: this.$parent.textChannel
		};
	}
};