import MessageHistory from "./MessageHistory";

export default class Channel {

	constructor(obj) {
		this.guildId = 0;
		this.id = 0;
		this.name = "";
		this.dataService = null;
		if (obj) Object.assign(this, obj);
		this.messageHistory = new MessageHistory(this.guildId, this.id, this.dataService);
	}

	sendMsg(msg) {
		this.dataService.sendMsg(this.guildId, this.id, msg);
	}
}